      this.drawer = !this.drawer;
    },
    onSwipeChange(index) {
      this.active = index;
    },
    onMenuSelet(index) {
      console.log(index);
      this.activeIndex = index;
      setTimeout(() => {
        this.activeIndex =
          {
            0: "00",
            4: "40"
          }[index] || index;
      }, 1000);
    },
    onSwiperImgClick(index = 0) {
      this.dialogSwipeImages = this["swiper" + this.activeIndex].map(
        item => item.src
      );
      this.imageShow = true;
      this.startPosition = index;
    },
    onImageChange(index) {
      this.imageIndex = index;
    }
  }
};
</script>

